import { useState, useRef, useEffect, useMemo } from "react";
import { useTranslation } from "react-i18next";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Label } from "@/components/ui/label";
import { Send, Trash2, Bot, User } from "lucide-react";

// Markdown rendering helper
const renderMarkdown = (content: string) => {
  if (!content) return null;

  const lines = content.split('\n');
  const elements: React.ReactNode[] = [];
  let inCodeBlock = false;
  let codeBlockContent: string[] = [];
  let codeBlockLang = '';
  let inTable = false;
  let tableRows: string[] = [];

  const processInlineMarkdown = (text: string): React.ReactNode => {
    // Process inline code, bold, italic, links
    const parts: React.ReactNode[] = [];
    let remaining = text;
    let key = 0;

    while (remaining.length > 0) {
      // Inline code
      const codeMatch = remaining.match(/`([^`]+)`/);
      if (codeMatch && codeMatch.index !== undefined) {
        if (codeMatch.index > 0) {
          parts.push(processTextWithBold(remaining.slice(0, codeMatch.index), key++));
        }
        parts.push(
          <code key={key++} className="px-1.5 py-0.5 rounded bg-muted-foreground/20 text-sm font-mono">
            {codeMatch[1]}
          </code>
        );
        remaining = remaining.slice(codeMatch.index + codeMatch[0].length);
        continue;
      }

      // Link
      const linkMatch = remaining.match(/\[([^\]]+)\]\(([^)]+)\)/);
      if (linkMatch && linkMatch.index !== undefined) {
        if (linkMatch.index > 0) {
          parts.push(processTextWithBold(remaining.slice(0, linkMatch.index), key++));
        }
        parts.push(
          <a
            key={key++}
            href={linkMatch[2]}
            target="_blank"
            rel="noopener noreferrer"
            className="text-primary underline hover:text-primary/80"
          >
            {linkMatch[1]}
          </a>
        );
        remaining = remaining.slice(linkMatch.index + linkMatch[0].length);
        continue;
      }

      parts.push(processTextWithBold(remaining, key++));
      break;
    }

    return parts.length === 1 ? parts[0] : <>{parts}</>;
  };

  const processTextWithBold = (text: string, keyPrefix: number): React.ReactNode => {
    const parts: React.ReactNode[] = [];
    let remaining = text;
    let key = 0;

    while (remaining.length > 0) {
      // Bold
      const boldMatch = remaining.match(/\*\*([^*]+)\*\*/);
      if (boldMatch && boldMatch.index !== undefined) {
        if (boldMatch.index > 0) {
          parts.push(<span key={`${keyPrefix}-${key++}`}>{remaining.slice(0, boldMatch.index)}</span>);
        }
        parts.push(<strong key={`${keyPrefix}-${key++}`} className="font-semibold">{boldMatch[1]}</strong>);
        remaining = remaining.slice(boldMatch.index + boldMatch[0].length);
        continue;
      }

      parts.push(<span key={`${keyPrefix}-${key++}`}>{remaining}</span>);
      break;
    }

    return parts.length === 1 ? parts[0] : <>{parts}</>;
  };

  const renderTable = (rows: string[]) => {
    if (rows.length < 2) return null;
    
    const parseRow = (row: string) => 
      row.split('|').filter(cell => cell.trim()).map(cell => cell.trim());
    
    const headers = parseRow(rows[0]);
    const dataRows = rows.slice(2).filter(row => !row.match(/^[\s|:-]+$/));

    return (
      <div className="overflow-x-auto my-2">
        <table className="min-w-full border-collapse text-sm">
          <thead>
            <tr className="border-b border-border">
              {headers.map((header, i) => (
                <th key={i} className="px-3 py-2 text-left font-semibold bg-muted/50">
                  {header}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {dataRows.map((row, i) => (
              <tr key={i} className="border-b border-border/50 hover:bg-muted/30">
                {parseRow(row).map((cell, j) => (
                  <td key={j} className="px-3 py-2">
                    {processInlineMarkdown(cell)}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  for (let i = 0; i < lines.length; i++) {
    const line = lines[i];
    const trimmedLine = line.trim();

    // Code block handling
    if (trimmedLine.startsWith('```')) {
      if (!inCodeBlock) {
        inCodeBlock = true;
        codeBlockLang = trimmedLine.slice(3).trim();
        codeBlockContent = [];
      } else {
        elements.push(
          <div key={i} className="my-2 rounded-md overflow-hidden">
            {codeBlockLang && (
              <div className="bg-muted-foreground/20 px-3 py-1 text-xs font-mono text-muted-foreground">
                {codeBlockLang}
              </div>
            )}
            <pre className="bg-muted-foreground/10 p-3 overflow-x-auto">
              <code className="text-sm font-mono">{codeBlockContent.join('\n')}</code>
            </pre>
          </div>
        );
        inCodeBlock = false;
        codeBlockLang = '';
      }
      continue;
    }

    if (inCodeBlock) {
      codeBlockContent.push(line);
      continue;
    }

    // Table handling
    if (trimmedLine.startsWith('|') && trimmedLine.endsWith('|')) {
      if (!inTable) {
        inTable = true;
        tableRows = [];
      }
      tableRows.push(trimmedLine);
      continue;
    } else if (inTable) {
      elements.push(<div key={`table-${i}`}>{renderTable(tableRows)}</div>);
      inTable = false;
      tableRows = [];
    }

    // Empty line
    if (!trimmedLine) {
      elements.push(<div key={i} className="h-2" />);
      continue;
    }

    // Headers
    if (trimmedLine.startsWith('### ')) {
      elements.push(
        <h3 key={i} className="text-base font-semibold mt-3 mb-1">
          {processInlineMarkdown(trimmedLine.slice(4))}
        </h3>
      );
      continue;
    }
    if (trimmedLine.startsWith('## ')) {
      elements.push(
        <h2 key={i} className="text-lg font-semibold mt-3 mb-1">
          {processInlineMarkdown(trimmedLine.slice(3))}
        </h2>
      );
      continue;
    }
    if (trimmedLine.startsWith('# ')) {
      elements.push(
        <h1 key={i} className="text-xl font-bold mt-3 mb-2">
          {processInlineMarkdown(trimmedLine.slice(2))}
        </h1>
      );
      continue;
    }

    // Horizontal rule
    if (trimmedLine === '---' || trimmedLine === '***') {
      elements.push(<hr key={i} className="my-3 border-border" />);
      continue;
    }

    // List items
    if (trimmedLine.match(/^[-*]\s/)) {
      elements.push(
        <div key={i} className="flex gap-2 ml-2">
          <span className="text-muted-foreground">â€¢</span>
          <span>{processInlineMarkdown(trimmedLine.slice(2))}</span>
        </div>
      );
      continue;
    }

    // Numbered list
    const numberedMatch = trimmedLine.match(/^(\d+)\.\s/);
    if (numberedMatch) {
      elements.push(
        <div key={i} className="flex gap-2 ml-2">
          <span className="text-muted-foreground min-w-[1.5rem]">{numberedMatch[1]}.</span>
          <span>{processInlineMarkdown(trimmedLine.slice(numberedMatch[0].length))}</span>
        </div>
      );
      continue;
    }

    // Regular paragraph
    elements.push(
      <p key={i} className="leading-relaxed">
        {processInlineMarkdown(trimmedLine)}
      </p>
    );
  }

  // Handle unclosed table
  if (inTable && tableRows.length > 0) {
    elements.push(<div key="table-final">{renderTable(tableRows)}</div>);
  }

  return <div className="space-y-1">{elements}</div>;
};

interface AgentDemoDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

interface Message {
  id: string;
  role: "user" | "assistant";
  content: string;
  isStreaming?: boolean;
}

const AgentDemoDialog = ({ open, onOpenChange }: AgentDemoDialogProps) => {
  const { t } = useTranslation();
  const [userId, setUserId] = useState("T1");
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [streamingContent, setStreamingContent] = useState("");
  const [streamingMessageId, setStreamingMessageId] = useState<string | null>(null);
  const scrollAreaRef = useRef<HTMLDivElement>(null);
  const abortControllerRef = useRef<AbortController | null>(null);

  // Auto-scroll to bottom when messages change
  useEffect(() => {
    if (scrollAreaRef.current) {
      const scrollContainer = scrollAreaRef.current.querySelector('[data-radix-scroll-area-viewport]');
      if (scrollContainer) {
        scrollContainer.scrollTop = scrollContainer.scrollHeight;
      }
    }
  }, [messages]);

  const handleClear = () => {
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
    setMessages([]);
    setStreamingContent("");
    setStreamingMessageId(null);
    setIsLoading(false);
  };

  const handleClose = () => {
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
    onOpenChange(false);
  };

  const handleSubmit = async () => {
    if (!input.trim() || isLoading) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: input.trim(),
    };

    setMessages((prev) => [...prev, userMessage]);
    setInput("");
    setIsLoading(true);

    // Create assistant message placeholder
    const assistantMessageId = (Date.now() + 1).toString();
    setStreamingMessageId(assistantMessageId);
    setStreamingContent("");
    setMessages((prev) => [
      ...prev,
      { id: assistantMessageId, role: "assistant", content: "", isStreaming: true },
    ]);

    try {
      abortControllerRef.current = new AbortController();

      // Call demo API directly
      const response = await fetch("https://xiaoxiongmarket.com/ai/demo/api/chat", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          message: userMessage.content,
          userId: userId,
        }),
        signal: abortControllerRef.current.signal,
      });

      if (!response.ok) {
        throw new Error(`API error: ${response.status}`);
      }

      const reader = response.body?.getReader();
      const decoder = new TextDecoder();

      if (reader) {
        let fullContent = "";

        while (true) {
          const { done, value } = await reader.read();
          if (done) break;

          const chunk = decoder.decode(value, { stream: true });
          fullContent += chunk;

          // ç›´æ¥æ›´æ–° streamingContent stateï¼Œä¸é€šè¿‡ messages
          setStreamingContent(fullContent);
          
          // æ·»åŠ å¾®å°å»¶è¿Ÿè®©æµè§ˆå™¨æœ‰æœºä¼šæ¸²æŸ“
          await new Promise(resolve => setTimeout(resolve, 0));
        }

        // å¤„ç† decoder å‰©ä½™å†…å®¹
        const remaining = decoder.decode();
        if (remaining) {
          fullContent += remaining;
        }

        // æµå¼ä¼ è¾“å®Œæˆï¼Œå°†æœ€ç»ˆå†…å®¹ä¿å­˜åˆ° messages
        setMessages((prev) =>
          prev.map((msg) =>
            msg.id === assistantMessageId
              ? { ...msg, content: fullContent, isStreaming: false }
              : msg
          )
        );
        setStreamingContent("");
        setStreamingMessageId(null);
      }
    } catch (error) {
      if ((error as Error).name === "AbortError") {
        console.log("Request aborted");
      } else {
        console.error("Chat error:", error);
        setMessages((prev) =>
          prev.map((msg) =>
            msg.id === assistantMessageId
              ? {
                  ...msg,
                  content: t("error.general", "å‘ç”Ÿé”™è¯¯ï¼Œè¯·é‡è¯•"),
                  isStreaming: false,
                }
              : msg
          )
        );
      }
      setStreamingContent("");
      setStreamingMessageId(null);
    } finally {
      setIsLoading(false);
      abortControllerRef.current = null;
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSubmit();
    }
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-[600px] h-[80vh] flex flex-col">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Bot className="h-5 w-5" />
            {t("admin.aiSettings.agentDemo", "Agent Demo æµ‹è¯•")}
          </DialogTitle>
        </DialogHeader>

        {/* User ID Input */}
        <div className="flex items-center gap-2 py-2">
          <Label htmlFor="demo-user-id" className="whitespace-nowrap">
            {t("admin.aiSettings.testUserId", "æµ‹è¯•ç”¨æˆ·ID")}:
          </Label>
          <Input
            id="demo-user-id"
            value={userId}
            onChange={(e) => setUserId(e.target.value)}
            className="flex-1"
            placeholder="T1"
          />
          <Button variant="outline" size="sm" onClick={handleClear}>
            <Trash2 className="h-4 w-4 mr-1" />
            {t("admin.aiSettings.clearChat", "æ¸…ç©º")}
          </Button>
        </div>

        {/* Chat Messages */}
        <ScrollArea ref={scrollAreaRef} className="flex-1 border rounded-md p-4">
          {messages.length === 0 ? (
            <div className="flex items-center justify-center h-full text-muted-foreground">
              {t("admin.aiSettings.startChat", "å‘é€æ¶ˆæ¯å¼€å§‹æµ‹è¯•")}
            </div>
          ) : (
            <div className="space-y-4">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex gap-2 ${
                    message.role === "user" ? "justify-end" : "justify-start"
                  }`}
                >
                  {message.role === "assistant" && (
                    <div className="w-8 h-8 rounded-full bg-primary/10 flex items-center justify-center flex-shrink-0">
                      <Bot className="h-4 w-4 text-primary" />
                    </div>
                  )}
                  <div
                    className={`max-w-[80%] rounded-lg px-4 py-2 ${
                      message.role === "user"
                        ? "bg-primary text-primary-foreground"
                        : "bg-muted"
                    }`}
                  >
                    {message.role === "user" ? (
                      <p className="whitespace-pre-wrap break-words">{message.content}</p>
                    ) : (
                      <div className="prose prose-sm dark:prose-invert max-w-none break-words">
                        {renderMarkdown(message.id === streamingMessageId ? streamingContent : message.content)}
                        {message.isStreaming && (
                          <span className="inline-block w-2 h-4 ml-1 bg-current animate-pulse" />
                        )}
                      </div>
                    )}
                  </div>
                  {message.role === "user" && (
                    <div className="w-8 h-8 rounded-full bg-secondary flex items-center justify-center flex-shrink-0">
                      <User className="h-4 w-4" />
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </ScrollArea>

        {/* Input Area */}
        <div className="flex gap-2 pt-2">
          <Input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder={t("admin.aiSettings.inputPlaceholder", "è¾“å…¥æ¶ˆæ¯...")}
            disabled={isLoading}
            className="flex-1"
          />
          <Button onClick={handleSubmit} disabled={isLoading || !input.trim()}>
            <Send className="h-4 w-4" />
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default AgentDemoDialog;
